import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Header from './components/layout/Header';
import './App.css';
import './components/Todo';
import Todo from './components/Todo';
import AddToDo from './components/AddToDo';
import About from './components/pages/About';
import uuid from 'uuid';

class App extends React.Component {
    state = {
        todo: [
            {
                id: uuid.v4(),
                title: 'Attend Developer meeting at 1pm in the large confrence room.',
                completed: false
            },
            {
                id: uuid.v4(),
                title: 'Make 3 commits on Git.',
                completed: false
            },
            {
                id: uuid.v4(),
                title: 'Get paper towels on the way home.',
                completed: false
            }
        ]
    }

    // Toggle Complete
    markComplete = (id) => {
        this.setState({ todo: this.state.todo.map(todos => {
            if (todos.id === id) {
                todos.completed = !todos.completed
            }
            return todos;
        }) });
    }

    // Delete Todo
    delTodo = (id) => {
        this.setState({ todo: [...this.state.todo.filter(todo => todo.id !== id)]})
    }

    // Add to do
    addToDo = (title) => {
        const newTodo = {
            id: uuid.v4(),
            title,
            completed: false
        }
        this.setState({ todo: [...this.state.todo, newTodo] })
    }

  render() {
      return (
        <Router>
            <div className="App">
                <div className="container">
                    <Header />
                      <Route exact path="/" render={props => (
                          <React.Fragment>
                              <AddToDo addToDo={this.addToDo} />
                              <Todo todo={this.state.todo} markComplete={this.markComplete}
                                  delTodo={this.delTodo}
                              />
                          </React.Fragment>
                      )} />
                      <Route path="/about" component={About} />
                </div>
            </div>
        </Router>
      );
   }
}

export default App;
